.flex {
  -webkit-box-flex: 1;
  -moz-box-flex: 1;
  box-sizing: border-box;
  flex: 1;
}

.blue-theme {
  background-color: $theme-blue !important;
}

.yellow-theme {
  background-color: $theme-yellow !important;
}

.blue-theme-text {
  color: $theme-blue !important;
}

.yellow-theme-text {
  color: $theme-yellow !important;
}

.headline {
  font-size: 36px;
  font-weight: 400;
  line-height: 32px;
}

p {
  font-size: 1.6em;
  font-weight: 400;
  letter-spacing: 0.010em;
  line-height: 1.4em;
  margin: 0.8em 0 1.6em;
}

body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
}